.registration-card {
    width: 700px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #f1efef;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    /* background-color: #c8daec; */
  }
  
  .registration-card h1 {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .registration-form label {
    display: block;
    margin-bottom: 10px;
  }
  
  .registration-form input,
  .registration-form button {
    width: 100%;
    padding: 8px;
    margin-top: 5px;
    margin-bottom: 15px;
    border: 1px solid #080808;
    border-radius: 3px;
    text-decoration: bold;
  }
  
  .ambulances {
    display: flex;
    flex-direction: column;
  }
  
  .ambulances > div {
    margin-bottom: 10px;
  }
  
  .register-button{
    background-color: rgb(137, 193, 249);
  }
  
  h1{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-decoration: wavy;
    font-size: 50px;
  }
 
