/* LoginForm.css */

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.login-background {
  width: 430px;
  height: 520px; /* Default height */
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}

.login-background .login-shape {
  height: 200px;
  width: 200px;
  position: absolute;
  border-radius: 50%;
}

.login-shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  right: -30px;
  bottom: -80px;
}

.login-form {
  height: 520px; /* Default height */
  width: 400px;
  background-color: rgba(255, 255, 255, 1); /* background color */
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.452); /* box shadow */
  padding: 50px 35px;
}

.login-form * {
  font-family: "Poppins", sans-serif;
  color: black; /* Change text color to black */
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}

.login-heading {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
  color: black;
}

.login-form label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}

.login-input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 300;
}

.login-input::placeholder {
  color: #e5e5e5;
}

.login-button {
  width: 100%;
}

.login-social {
  margin-top: 30px;
  display: flex;
  cursor: pointer;
}

.login-social div {
  width: 900px;
  border-radius: 3px;
  padding: 5px 10px 10px 10px;
  color: #eaf0fb;
  text-align: center;
}

.login-social b {
  margin-right: 4px;
  font-size: 15px;
  color: #ff512f;
}

.signup-mode .login-background {
  height: 650px; /* Increased height for signup mode */
}

.signup-mode .login-form {
  height: 650px; /* Increased height for signup mode */
}
